// Buttons
//
// Easily pump out default styles, as well as :hover, :focus, :active,
// and disabled options for all buttons
//
// Button Styles
@mixin button-variant($color, $background, $border, $hover-color, $hover-background, $hover-border, $border-radius) {
	color: $color;
	background-color: $background;
	border: $border;
	border-radius: $border-radius;
	&:hover {
		color: $hover-color;
		background-color: $hover-background;
	    border: $hover-border;
	    box-shadow: $btn-active-box-shadow;
	}
	&:focus,
	&.focus {
		color: $hover-color;
		background-color: $hover-background;
	    border: $hover-border;
	    box-shadow: $btn-active-box-shadow;
	}
	&:active,
	&.active {
		color: $hover-color;
		background-color: $hover-background;
	    border: $hover-border;
		&:hover,
		&:focus,
		&.focus {
		  	color: $color;
		  	background-color: darken($background, 17%);
		    border: $hover-border;
		    box-shadow: $btn-active-box-shadow;
		}
	}
	&.disabled,
	&:disabled {
		background-color: $btn-link-disabled-color;
		border: $btn-link-disabled-color;
		&:focus,
		&.focus {
			background-color: $btn-link-disabled-color;
			border: $btn-link-disabled-color;
		}
		&:hover {
			background-color: $btn-link-disabled-color;
			border: $btn-link-disabled-color;
		}
	}
}