//
/* Print */
//
// scss-lint:disable ImportantRule, QualifyingElement
// Source: https://github.com/h5bp/html5-boilerplate/blob/master/src/css/main.css
@if $enable-print-styles {
    @media print {
        *,
        *::before,
        *::after,
        *::first-letter,
        *::first-line {
    		// Hydrogen specific; comment out `color` and `background`
    		//color: #000 !important; // Black prints faster:
    		                        //   http://www.sanbeiji.com/archives/953
    		text-shadow: none !important;
    		//background: transparent !important;
    		box-shadow: none !important;
        }

        a,
        a:visited {
        	text-decoration: underline;
        }

        // Hydrogen specific; comment the following selector out
        //a[href]::after {
        //  content: " (" attr(href) ")";
        //}

        abbr[title]::after {
        	content: " (" attr(title) ")";
        }

        // Hydrogen specific; comment the following selector out
        //
        // Don't show links that are fragment identifiers,
        // or use the `javascript:` pseudo protocol
        //

        //a[href^="#"]::after,
        //a[href^="javascript:"]::after {
        //  content: "";
        //}

        pre,
        blockquote {
    		border: $border-width solid #999;   // Hydrogen custom code; using `$border-width` instead of 1px
    		page-break-inside: avoid;
        }

        //
        // Printing Tables:
        // http://css-discuss.incutio.com/wiki/Printing_Tables
        //

        thead {
        	display: table-header-group;
        }

        tr,
        img {
        	page-break-inside: avoid;
        }

        p,
        h2,
        h3 {
        	orphans: 3;
        	widows: 3;
        }

        h2,
        h3 {
        	page-break-after: avoid;
        }

        // Hydrogen specific changes start

        // Hydrogen components
        .navbar {
        	display: none;
        }
        .btn,
        .dropup > .btn {
    	> .caret {
    		border-top-color: #000 !important;
    	}
        }
        .tag {
        	border: $border-width solid #000;
        }

        .table {
    		border-collapse: collapse !important;
    		td,
    		th {
    			background-color: #fff !important;
    		}
        }
        .table-bordered {
    		th,
    		td {
    			border: 1px solid #ddd !important;
    		}
        }

        // Hydrogen specific changes end
    }
}