//
/* Typography */
//
// Headings
h1, 
h2, 
h3, 
h4, 
h5, 
h6 {
	margin-bottom: $headings-margin-bottom;
	font-family: $headings-font-family;
	font-weight: $headings-font-weight;
	line-height: $headings-line-height;
	color: $headings-color;
}

h1 { font-size: $font-size-h1; }
h2 { font-size: $font-size-h2; }
h3 { font-size: $font-size-h3; }
h4 { font-size: $font-size-h4; }
h5 { font-size: $font-size-h5; }
h6 { font-size: $font-size-h6; }

%lead,
.lead {
	font-size: $lead-font-size;
	font-weight: $lead-font-weight;
}

// Horizontal rules
//
hr {
	margin-top: $spacer-y;
	margin-bottom: $spacer-y;
	border: 0;
	border-top: $hr-border-width solid $hr-border-color;
}

// Emphasis
//
small,
.small,
%small {
	font-size: $small-font-size;
	font-weight: normal;
}

// Paragraphs
//
p { margin: 0 0 $spacer-y; }

// Lists
//
ul,
ol {
	padding-left: $list-padding-left;
	margin: 0 0 $spacer-y;
}

ol {
	@if $enable-colored-ol == true {
		@include orderedlistcolor($ol-number-color);
	}
}

ul {
	@if $list-style == 'img' {
		list-style-image: url($list-bullet-img);
	} @else {
		list-style: $list-style; 
	}

	@if $enable-header-bullets == false {
		header & { 
			padding-left: 0;
			list-style: none; 
		}
	}
	@if $enable-footer-bullets == false {
		footer & {
			padding-left: 0;
			list-style: none;  
		}
	}
}
.list-unstyled,
%list-unstyled {
	@include list-unstyled();
}

// Inline turns list items into inline-block
.list-inline,
%list-inline {
	@include list-unstyled();
	li {
		display: inline-block;
		&:not(:last-child) {
			margin-right: $list-inline-margin;
		}
	}
}

// Blockquotes
@if $enable-blockquotes == true {
	blockquote { @extend %blockquote; }
}
.blockquote,
%blockquote {
	padding: ($spacer / 2) $spacer;
	margin-bottom: $spacer-y;
	font-size: $blockquote-font-size;
	border-left: $blockquote-border-width solid $blockquote-border-color;
}
.blockquote-footer,
%blockquote-footer {
	display: block;
	font-size: 80%; // back to default font-size
	color: $blockquote-small-color;
	&::before {
		content: "\2014 \00A0"; // em dash, nbsp
	}
}
// Opposite alignment of blockquote
.blockquote-reverse,
%blockquote-reverse {
	padding-right: $spacer;
	padding-left: 0;
	text-align: right;
	border-right: $blockquote-border-width solid $blockquote-border-color;
	border-left: 0;
	.blockquote-footer {
		&::before {
			content: "";
		}
		&::after {
			content: "\00A0 \2014"; // nbsp, em dash
		}
	}
}

// Description Lists
//
// 1 Remove browser default
// 2 Undo browser default
// 3 Clean up some horizontal `<dl>`s built with grids
dl { margin-top: 0; } // 1
dt,
dd { line-height: $line-height-base; }
dt { font-weight: bold; }
dd { margin-left: 0; }  // 2
dl.row {
	> dd + dt { clear: left; }
}

// Address
//
address {
	margin-bottom: $spacer-y;
	line-height: $line-height-base;
	font-style: normal;
}
